import React, { useState } from "react";
import "./index.scss";
import { Link } from "react-router-dom";

import {
  CCarousel,
  CCarouselIndicators,
  CCarouselInner,
  CCarouselItem,
  CNavbar,
  CToggler,
  CNavbarBrand,
  CCollapse,
  CNavbarNav,
  CContainer,
} from "@coreui/react";

import LogoPage from "../../../assets/icons/logo-dark.svg";

import Slider1 from "../../../assets/images/slider1.jpg";
import Slider2 from "../../../assets/images/slider2.jpg";
import Slider3 from "../../../assets/images/slider3.jpeg";
import Slider4 from "../../../assets/images/slider4.jpg";
import Slider5 from "../../../assets/images/slider5.jpg";
import Slider6 from "../../../assets/images/slider6.jpg";
import Slider7 from "../../../assets/images/slider7.jpg";
import Slider8 from "../../../assets/images/slider8.jpg";

const slides = [
  Slider1,
  Slider2,
  Slider3,
  Slider4,
  Slider5,
  Slider6,
  Slider7,
  Slider8,
];

const NewHomeContent = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="newHomeContent">
      <div className="newHeader">
        <CContainer>
          <CNavbar expandable="md" color="light" className=" navbar-light ">
            <CToggler
              className="navMobile"
              color="white"
              inNavbar
              onClick={() => setIsOpen(!isOpen)}
            />
            <CNavbarBrand>
              <Link to="/" className="logoPage">
                <img src={LogoPage} alt="" />
              </Link>
            </CNavbarBrand>
            <CCollapse show={isOpen} navbar>
              <CNavbarNav className="ml-auto"></CNavbarNav>
              <CNavbarNav className="ml-auto rightMenu">
                <Link to="/projects">Projects</Link>
                <Link to="/bio">Bio</Link>
                <Link to="/dashboard">Account</Link>
              </CNavbarNav>
            </CCollapse>
          </CNavbar>
        </CContainer>
      </div>
      
      
      <CCarousel
        autoPlay={true}
        interval={100}
        autoSlide={3000}
        animate={true}
      >
        <CCarouselIndicators />

        <CCarouselInner>
          {slides &&
            slides.map((item, i) => (
              <CCarouselItem key={i}>
                <img className="sliderImage" src={item} alt="" />
              </CCarouselItem>
            ))}
        </CCarouselInner>
      </CCarousel>
    </div>
  );
};

export default NewHomeContent;
